<template>
<transition name="slide-fade">
	<div>
		<div class="cc-tel" @click="goto1"><img ref="img" src="../assets/img/tel2.png" alt=""></div>
		<img class="cc-logo" ref="img" src="../assets/img/listbg.png" alt="">
		<div class="cc-ordering" ref="box">
			<div class="cc-tabbar flexstart"  :class="{'is_fixed' : isFixed}">
				<div class="cc-l">求职意向</div>
				<div class="cc-r flexstart" @click="tabbar1">
					<span class="van-ellipsis">{{areadata!=""?areadata:"区域"}}</span>
					<van-icon class="cc-icon" size="16px" color="#888888" v-show="areadata==''" name="arrow-down" />
					<van-icon class="cc-icon" size="16px" color="#888888" v-show="areadata!=''" name="arrow-up" />
				</div>
				<div class="cc-r flexstart" @click="tabbar2">
					<span class="van-ellipsis">{{gangwei!=""?gangwei:"岗位"}}</span>
					<van-icon class="cc-icon" size="16px" color="#888888" v-show="gangwei==''" name="arrow-down" />
					<van-icon class="cc-icon" size="16px" color="#888888" v-show="gangwei!=''" name="arrow-up" />
				</div>
				<div class="cc-r flexstart" @click="tabbar3">
					<span class="van-ellipsis">{{price!=""?price:"薪资"}}</span>
					<van-icon class="cc-icon" size="16px" color="#888888" v-show="price==''" name="arrow-down" />
					<van-icon class="cc-icon" size="16px" color="#888888" v-show="price!=''" name="arrow-up" />
				</div>
			</div>
			<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
				<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
					<div class="cc-list spacebetween" v-for="(item,index) in list" :key="index">
						<div class="cc-l" @click="goto(item.jobId)">
							<div class="cc-name van-ellipsis">{{item.name}}</div>
							<div class="cc-price flexstart">
								<div>{{item.payStr=="面议"?item.payStr:item.payStr+'/月'}}</div>
								<span> · {{item.num}} 人</span>
							</div>
							<div class="cc-tags flexstart">
								<span  v-for="(item1,index1) in item.welfares" :key="index1">{{item1}}</span>
							</div>
							<div class="cc-company van-ellipsis">{{item.companyName}}</div>
						</div>
						<div class="cc-r">
							<span>{{item.district}}</span>
							<div v-show="tgbrole1==1&&!item.delivery" @click="jobDelivery(item.jobId,index)">申请</div>
							<div v-show="item.delivery" class="active">已申请</div>
						</div>
					</div>
				</van-list>
			</van-pull-refresh>
		</div>
		<van-popup v-model="showPicker2" position="bottom"><van-picker show-toolbar :columns="columns2" @cancel="showPicker2 = false" @confirm="onConfirm2" /></van-popup>
		<van-popup v-model="show1" position="bottom"><van-picker show-toolbar :columns="columns1" @cancel="onCancel1" @confirm="onConfirm1" /></van-popup>
		<van-popup v-model="showPicker" position="bottom"><van-picker show-toolbar :columns="columns" @cancel="showPicker = false" @confirm="onConfirm" /></van-popup>
	</div>
</transition>
</template>

<script>
import {PullRefresh,List,Icon ,Popup ,Picker ,Toast,Dialog} from 'vant';
export default {
	name: 'Home',
	components: {
		[PullRefresh.name]: PullRefresh,
		[List.name]: List,
		[Icon.name]: Icon,
		[Popup.name]: Popup,
		[Picker.name]: Picker,
		[Dialog.name]: Dialog,
		[Toast.name]: Toast,
	},
	data() {
		return {
			pageNum:1,
			pageSize:6,
			list: [],
			loading: false,
			finished: false,
			refreshing: false,
			isFixed:false,
			offsetTop: 300,

			showPicker2:false,//区域
			areacode:'',
			areadata:"",
			columns2:["不限","海曙区","江北区","北仑区","镇海区","鄞州区","奉化区","象山县","宁海县","余姚市","慈溪市"],
			columns3:["","330203","330205","330206","330211","330212","330213","330225","330226","330281","330282"],

			show1:false,
			columns1: [],
			columns01: [],
			gangwei:"",
			gangwei1:"",

			showPicker: false,//薪资
			price:"",
			price1:"",
			columns: [],
			columns4: [],
			tgbrole1:""
		};
	},
	mounted() {
		window.addEventListener('scroll', this.initHeight);
		this.$nextTick(() => {
			this.offsetTop=this.$refs.img.offsetWidth*150/375;
		})
		this.jobCat();
		this.jobSalary();
		this.tgbrole1=localStorage.getItem("tgbrole1");
	},
	methods: {
		goto1(){
			this.$router.push({
				path:"/tel"
			})
		},
		goto(rt){
			this.$router.push({
				path:"/detail?id="+rt
			})
		},
		initHeight () {
			var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
			this.isFixed = scrollTop > this.offsetTop ? true : false;
		},
		jobCat(){
			var that=this;
			this.$http.get('/main/jobCat').then(function (res) {
				if (res.code==10000) {
					let arr=["不限"];
					let arr1=[{id:""}];
					arr1.push(...res.data)
					res.data.forEach(function(element) {
						arr.push(element.name)
					});
					that.columns1=arr;
					that.columns01=arr1;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		jobSalary(){
			var that=this;
			this.$http.get('/main/jobSalary').then(function (res) {
				if (res.code==10000) {
					let arr=["不限"];
					let arr1=[{id:""}];
					arr1.push(...res.data)
					res.data.forEach(function(element) {
						arr.push(element.name)
					});
					that.columns=arr;
					that.columns4=arr1;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		onLoad() {
			if (this.refreshing) {
				this.list = [];
				this.refreshing = false;
			}
			var that=this;
			this.$http.get(`/main/searchWork/${that.pageNum}/${that.pageSize}?district=${that.areacode}&jobCat=${that.gangwei1}&salary=${that.price1}`).then(function (res) {
				that.loading = false;
				if (res.code==10000) {
					that.list.push(...res.data.list)
					if (res.data.hasNextPage) {
						that.pageNum=res.data.pageNum+1
					}else{
						that.finished = true;
					}
				}else{
					that.finished = true;
					Toast.fail(res.msg);
				}
			}).catch(function () {
				that.loading = false;
			});
		},
		jobDelivery(a,i) {
			var that=this;
			this.$http.get(`/main/jobDelivery?jobId=${a}`).then(function (res) {
				if (res.code==10000) {
					that.list[i].delivery=true
					Dialog.alert({
						title: '提示',
						message: '您已报名成功，我们会尽快与您联系，请保持手机畅通！',
						confirmButtonText:'我知道了'
					}).then(() => {
					});
				}else if(res.code==30002){
					let isrole = localStorage.getItem("tgbrole1");
					if (isrole==1) {
						Dialog.confirm({
							title: '提示',
							message: '您尚未填写个人信息，无法申请岗位，请完善个人信息。'
						}).then(() => {
							that.$router.push({
								path:"/home"
							})
						}).catch(() => {
						});
					}else{
						Toast.success(res.msg);
					}
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		onRefresh() {
			// 清空列表数据
			this.finished = false;
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.loading = true;
			this.pageNum = 1;
			this.onLoad();
		},
		tabbar1(){
			this.showPicker2 = true;
			this.$refs.box.scrollIntoView();
		},
		onConfirm2(res,index){
			this.areacode=this.columns3[index];
			this.areadata=res
			this.showPicker2 = false;
			this.onRefresh();
			this.list = [];
		},

		tabbar2(){
			this.show1 = true;
			this.$refs.box.scrollIntoView();
		},
		onConfirm1(value,index) {
			this.show1 = false;
			this.gangwei=value;
			this.gangwei1=this.columns01[index].id;
			this.onRefresh();
			this.list = [];
		},
		onCancel1() {
			this.show1 = false;
		},
		tabbar3(){
			this.showPicker = true;
			this.$refs.box.scrollIntoView();
		},
		onConfirm(res,index){
			this.showPicker = false;
			this.price=res;
			if (index>0) {
				this.price1=this.columns4[index].id;
			}else{
				this.price1=""
			}
			this.onRefresh();
			this.list = [];
		},
	},
	destroyed () {
		window.removeEventListener('scroll', this.initHeight)
	}
}
</script>
